



import { Component, Vue } from 'vue-property-decorator';
import { successIcon } from '@/utils/svgIcons';

@Component({
  components: {},
})
export default class Toasted extends Vue {
  showSuccess(message) {
    this.$toasted.show(message, {
      position: 'top-center',
      duration: 1500,
      className: ['bb-toasted', 'bb-toasted--success'],
      fitToScreen: true,
      fullWidth: true,
      iconPack: 'callback',
      icon: (el) => {
        el.innerHTML = successIcon;
        return el;
      },
    });
  }

  showError(message) {
    this.$toasted.show(message, {
      position: 'top-center',
      duration: 1500,
      className: ['bb-toasted', 'bb-toasted--error'],
      fitToScreen: true,
      fullWidth: true,
      iconPack: 'callback',
    });
  }
}
